body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  margin: 0px;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.title{
  margin-top: 150px;
  text-align: center;
  font-weight: bolder;
  font-size: 40px;
}

.first {

  display: grid;
  grid-template-columns: 33% 33% 33%;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
}

.second {
  width: 400px;
}
.reset{
  height: 40px;
  width: 100px;
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
}
.second img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-left: 70px;
}

.second h1 {
  font-size: 20px;
  margin: 10px 0;
  text-align: justify;
  margin-left: 70px;
}

.second button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.second button:hover {
  background-color: #45a049;
}

.second button:active {
  background-color: #3e8e41;
}

.reset-button {
  background-color: #f44336;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s;
}

.reset-button:hover {
  background-color: #e53935;
}

h1 {
  font-size: 24px;
  color: #333;
}
